import React, { useState } from "react";

import { Link } from "react-router-dom";

const Navbar = () => {
  const [menu, setMenu] = useState(true);

  const activeMenu = menu ? "" : "is-active";

  const handleMenu = (e) => {
    setMenu(!menu);
  };

  return (
    <nav
      className="container navbar is-transparent"
      role="navigation"
      aria-label="main navigation"
    >
      <div className="navbar-brand">
        <a className="navbar-item" href="/">
          <img src="logo192.png" className="img-logo" alt="logo" />
        </a>
        <a
          href="/"
          role="button"
          className={`navbar-burger ${activeMenu}`}
          aria-label="menu"
          aria-expanded="false"
          onClick={(e) => {
            e.preventDefault();
            handleMenu();
          }}
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </a>
      </div>
      <div className={`navbar-menu container-menu ${activeMenu}`}>
        <div className="navbar-end">
          <Link to="/">
            <p className="navbar-item" href="/" onClick={handleMenu}>
              Inicio
            </p>
          </Link>
          <Link to="/conoceme">
            <p className="navbar-item" href="/" onClick={handleMenu}>
              Conóceme
            </p>
          </Link>
          <Link to="/proyectos">
            <p className="navbar-item" href="/" onClick={handleMenu}>
              Proyectos
            </p>
          </Link>
          <Link to="/contacto">
            <p className="navbar-item" href="/" onClick={handleMenu}>
              Contacto
            </p>
          </Link>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
